/**
 * ajax
 * @param {*} props 
 * @param {*} config 
 */
function showErrorMessageByRes(props, res) {
	if(res && res.message ){
		const colors = ['danger', 'warning', 'info', 'success'];
		let color = colors.indexOf(res.type) != -1 ? res.type : 'danger';
		toast({ color, content : res.message  });
	}
	
}
